import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { HomeComponent } from './home/home.component';

const appRoutes: Routes = [
  {path:'posts', loadChildren: './posts/posts.module#PostsModule'},
  {path:'comments', loadChildren: './comments/comments.module#CommentsModule'},
  {path:'albums', loadChildren: './albums/albums.module#AlbumsModule'},
  {path:'photos', loadChildren: './photos/photos.module#PhotosModule'},
  {path:'todos', loadChildren: './todos/todos.module#TodosModule'},  
  {path:'users', loadChildren: './users/users.module#UsersModule'},  
  {path:'home', component:HomeComponent}
]

@NgModule({
  imports: [
    RouterModule.forRoot(appRoutes)
  ],
  exports:[
    RouterModule
  ],
  declarations: []
})
export class AppRoutingModule { }
