import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import * as _ from 'underscore';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/do';


@Injectable()
export class CommonService {

  dbUrl:string ="https://jsonplaceholder.typicode.com/";
  constructor(private http:HttpClient) { }

  getPosts():Observable<any>{   
    return this.http.get(this.dbUrl+'posts')
  }
  getComments():Observable<any>{   
    return this.http.get(this.dbUrl+'comments')
  }
  
  getAlbums():Observable<any>{   
    return this.http.get(this.dbUrl+'albums')
  }
  
  getPhotos():Observable<any>{   
    return this.http.get(this.dbUrl+'photos')
  }
  
  getTodos():Observable<any>{   
    return this.http.get(this.dbUrl+'todos')
  }
  getUsers():Observable<any>{   
     return this.http.get(this.dbUrl+'users')
  }
  getUserId(id):Observable<any>{
    return this.http.get(this.dbUrl+'users/'+[id] )
  }



  getPager(totalItems: number, currentPage: number = 1, pageSize: number = 5) {
    // calculate total pages
    let totalPages = Math.ceil(totalItems / pageSize);

    let startPage: number, endPage: number;
    
    if (totalPages <= 5) {
        startPage = 1;
        endPage = totalPages;
    } else {
        if (currentPage <= 3) {
            startPage = 1;
            endPage = 5;
        } else if (currentPage + 1 >= totalPages) {
            startPage = totalPages - 4;
            endPage = totalPages;
        } else {
            startPage = currentPage - 2;
            endPage = currentPage+2;
        }
    }

    // calculate start and end item indexes
    let startIndex = (currentPage - 1) * pageSize;
    let endIndex = Math.min(startIndex + pageSize - 1, totalItems - 1);

    // create an array of pages to ng-repeat in the pager control
    let pages = _.range(startPage, endPage + 1);

    // return object with all pager properties required by the view
    return {
        totalItems: totalItems,
        currentPage: currentPage,
        pageSize: pageSize,
        totalPages: totalPages,
        startPage: startPage,
        endPage: endPage,
        startIndex: startIndex,
        endIndex: endIndex,
        pages: pages
    };
}
}
